  async function getStopTime() {
    setIsLoading(prev => ({ ...prev, stopTime: true }));
    try {
      // 检查是否为移动设备使用WalletConnect
      if (WalletService.walletType === 'walletconnect') {
        try {
          const readContract = await getReadOnlyContract();
          const time = await readContract.getStopTime();
          setStopTime(time);
          return;
        } catch (error) {
          console.error("使用只读合约获取停止时间失败:", error);
          // 继续尝试标准方法
        }
      }
      
      // 标准方法
      const contract = await getContract();
      const stopTime = await contract.getStopTime();
      // BigInt处理
      setStopTime(stopTime);
    } catch (error) {
      console.error("Error fetching stop time:", error);
    } finally {
      setIsLoading(prev => ({ ...prev, stopTime: false }));
    }
  }

  async function getDrawTime() {
    setIsLoading(prev => ({ ...prev, drawTime: true }));
    try {
      // 检查是否为移动设备使用WalletConnect
      if (WalletService.walletType === 'walletconnect') {
        try {
          const readContract = await getReadOnlyContract();
          const time = await readContract.getDrawTime();
          setDrawTime(time);
          return;
        } catch (error) {
          console.error("使用只读合约获取开奖时间失败:", error);
          // 继续尝试标准方法
        }
      }
      
      // 标准方法
      const contract = await getContract();
      const drawTime = await contract.getDrawTime();
      // BigInt处理
      setDrawTime(drawTime);
    } catch (error) {
      console.error("Error fetching draw time:", error);
    } finally {
      setIsLoading(prev => ({ ...prev, drawTime: false }));
    }
  }

  // 添加断开钱包连接功能
  async function disconnectWallet() {
    try {
      await WalletService.disconnect();
      setAccount(null);
    } catch (error) {
      console.error("Error disconnecting wallet:", error);
    }
  }

  // 添加一个重试所有数据的函数
  async function refreshAllContractData() {
    try {
      console.log("Manually refreshing all contract data...");
      setDataFetchFailed(false);
      
      // 强制使用只读合约获取数据
      try {
        console.log("使用只读合约获取所有数据...");
        const readContract = await getReadOnlyContract();
        
        // 获取奖金额
        const prizeAmount = await readContract.getPrizeAmount();
        setPrizeAmount(ethers.formatEther(prizeAmount));
        console.log("成功获取奖金额:", prizeAmount);
        
        // 获取票数
        const ticketCount = await readContract.getTicketCount();
        setTicketCount(ticketCount);
        console.log("成功获取票数:", ticketCount);
        
        // 获取当前轮次
        const round = await readContract.round();
        setCurrentRound(round);
        console.log("成功获取当前轮次:", round);
        
        // 获取开放状态
        const isOpen = await readContract.isOpenLottery();
        setIsOpenLottery(isOpen);
        console.log("成功获取开放状态:", isOpen);
        
        // 获取停止时间
        const stopTime = await readContract.getStopTime();
        setStopTime(stopTime);
        console.log("成功获取停止时间:", stopTime);
        
        // 获取开奖时间
        const drawTime = await readContract.getDrawTime();
        setDrawTime(drawTime);
        console.log("成功获取开奖时间:", drawTime);
        
        // 获取上次赢家
        const lastWinner = await readContract.getLastWinner();
        setLastWinner(lastWinner);
        console.log("成功获取上次赢家:", lastWinner);
        
        return;
      } catch (error) {
        console.error("只读合约获取数据失败:", error);
        // 如果只读方式失败，尝试按顺序获取
      }
      
      // 按顺序重试一下以提高成功率
      await getPrizeAmount();
      await getTicketCount();
      await getCurrentRound();
      await getIsOpenLottery();
      await getStopTime();
      await getDrawTime();
      await getLastWinner();
    } catch (error) {
      console.error("Error refreshing contract data:", error);
      setDataFetchFailed(true);
    }
  }

  // 添加一个直接检查彩票状态的方法，并在页面显示
  async function checkLotteryStatus() {
    try {
      const readContract = await getReadOnlyContract();
      const isOpen = await readContract.isOpenLottery();
      setIsOpenLottery(isOpen);
      
      const stopTime = await readContract.getStopTime();
      setStopTime(stopTime);
      
      const currentTime = Math.floor(Date.now() / 1000);
      const timeLeft = Number(stopTime) - currentTime;
      
      console.log("彩票状态详情:");
      console.log("- 开放状态:", isOpen);
      console.log("- 停止时间:", new Date(Number(stopTime) * 1000).toLocaleString());
      console.log("- 剩余时间:", timeLeft > 0 ? `${Math.floor(timeLeft / 3600)}小时${Math.floor((timeLeft % 3600) / 60)}分钟` : "已结束");
      
      return { isOpen, stopTime, timeLeft };
    } catch (error) {
      console.error("检查彩票状态失败:", error);
      return null;
    }
  }

  // 获取当前轮次的函数
  async function getCurrentRound() {
    setIsLoading(prev => ({ ...prev, currentRound: true }));
    try {
      // 检查是否为移动设备使用WalletConnect
      if (WalletService.walletType === 'walletconnect') {
        try {
          const readContract = await getReadOnlyContract();
          const round = await readContract.round();
          setCurrentRound(round);
          return;
        } catch (error) {
          console.error("使用只读合约获取当前轮次失败:", error);
          // 继续尝试标准方法
        }
      }
      
      // 标准方法
      const contract = await getContract();
      const round = await contract.round();
      setCurrentRound(round);
    } catch (error) {
      console.error("Error fetching current round:", error);
    } finally {
      setIsLoading(prev => ({ ...prev, currentRound: false }));
    }
  }

